	import share from '@/components/share';
    import {
        mapState,mapMutations
    } from 'vuex';
	export default{
		components: {
			share
		},
		data() {
			return {
			    currentId:"",

				specClass: 'none',
				specSelected:[],

				fruitInfoDetail:[{
				}],

				favorite: true,
				shareList: [],
				specList: [
					{
						id: 1,
						name: '尺寸',
					},
					{	
						id: 2,
						name: '颜色',
					},
				],
				specChildList: [
					{
						id: 1,
						pid: 1,
						name: 'XS',
					},
					{
						id: 2,
						pid: 1,
						name: 'S',
					},

				]
			};
		},
        computed: {
            ...mapState(['hasLogin','userInfo'])
        },
		async onLoad(options){
			//接收传值,id里面放的是标题，因为测试数据并没写id 
			// 接受url: `/pages/product/product?id=${id}`
			this.currentId = options.id
			let id = options.id;
			if(id){
                this.$http.post("getFruitInfoDetail?id="+ id).then((res) => {
                    console.log("success")
                    console.log(res)
                    if (res.data.status == "success") {
                        var fruitInfoDetail = res.data.data;
                        this.fruitInfoDetail = fruitInfoDetail;
                    }else {
                        this.$api.msg(res.data.message);
                    }
                }).catch(error => {
                    console.log(error)
                }).finally(() => {

                })
			}
			//规格 默认选中第一条
			this.specList.forEach(item=>{
				for(let cItem of this.specChildList){
					if(cItem.pid === item.id){
						this.$set(cItem, 'selected', true);
						this.specSelected.push(cItem);
						break; //forEach不能使用break
					}
				}
			})
			this.shareList = await this.$api.json('shareList');
		},

		methods:{

            addToCart(){
                if(!this.hasLogin) {
                    var url = '/pages/public/login';
                    uni.navigateTo({
                        url
                    })
                    return
                }

                var form_query = {
                    userId:this.userInfo.id,
					fruitId:this.currentId
				}
                var params = this.jsonToStr(form_query)
                this.$http.post("cart/addToCart?"+ params).then((res) => {
                    console.log("success")
                    console.log(res)
                    if (res.data.status == "success") {
                        this.$api.msg(res.data.message);
                    }else {
                        this.$api.msg(res.data.message);
                    }
                }).catch(error => {
                    console.log(error)
                    return
                }).finally(() => {

                })


			},
			//规格弹窗开关
			toggleSpec() {
				if(this.specClass === 'show'){
					this.specClass = 'hide';
					setTimeout(() => {
						this.specClass = 'none';
					}, 250);
				}else if(this.specClass === 'none'){
					this.specClass = 'show';
				}
			},
			//选择规格
			selectSpec(index, pid){
				let list = this.specChildList;
				list.forEach(item=>{
					if(item.pid === pid){
						this.$set(item, 'selected', false);
					}
				})

				this.$set(list[index], 'selected', true);
				//存储已选择
				/**
				 * 修复选择规格存储错误
				 * 将这几行代码替换即可
				 * 选择的规格存放在specSelected中
				 */
				this.specSelected = []; 
				list.forEach(item=>{ 
					if(item.selected === true){ 
						this.specSelected.push(item); 
					} 
				})
				
			},
			//分享
			share(){
				this.$refs.share.toggleMask();	
			},
			//收藏
			toFavorite(){
				this.favorite = !this.favorite;
			},
			buy(){
                if(!this.hasLogin){
                    var url = '/pages/public/login';
                    uni.navigateTo({
                        url
                    })
                }else {
                    uni.navigateTo({
                        url: `/pages/order/createOrder?data=${JSON.stringify({
                            fruitInfoDetail: this.fruitInfoDetail
                        })}`
                    })
				}

			},
			stopPrevent(){},
            jsonToStr(json) {
                let str = "";
                for (let key in json) {
                    str += "&" + key + "=" + json[key];
                }
                return str.substring(1);
            },
		},

	}
